.slider {
  padding-bottom: 39px;
  margin-left: -20px;
  margin-right: -20px;

  @media (min-width: $tablet-width) {
    padding-bottom: 67px;
    margin-left: -30px;
    margin-right: -30px;
  }

  @media (min-width: $desktop-width) {
    padding: 0;
    margin: 0;
  }
}

.slider__list {
  list-style: none;
  margin: 0;
  padding: 0;
}

.slider__item {
  text-align: center;

  &--hidden {
    display: none;
  }
}

.slider__item img {
  @media (min-width: $tablet-width) {
    width: 670px;
    height: 520px;
    margin-top: -160px;
  }

  @media (min-width: $desktop-width) {
    margin-top: -55px;
    width: 647px;
  }
}
