.input-text {
  padding: 0 17px;
  border: none;
  border-bottom: 1px solid $border-color--light;

  @media (min-width: $tablet-width) {
    padding: 0 70px;
    border: none;
    margin-bottom: 44px;
  }

  @media (min-width: $desktop-width) {
    width: 570px;
    margin-right: 76px;
    padding: 0;
  }
}

.input-text__list {
  padding: 0;
  margin: 0;
  list-style: none;
  font-size: 16px;
  text-transform: uppercase;
  padding-bottom: 9px;

  @media (min-width: $desktop-width) {
    padding-bottom: 5px;
  }
}

.input-text__label {
  display: flex;
  flex-wrap: wrap;
  justify-content: flex-start;
  align-items: center;
  margin-bottom: 15px;

  @media (min-width: $tablet-width) {
    font-size: 20px;
    line-height: 30px;
    margin-bottom: 23px;
  }
}

.input-text__input {
  padding: 12px 15px;
  margin-left: 15px;
  border: 1px solid $gray--dark;
  display: flex;
  flex-grow: 1;

  @media (min-width: $tablet-width) {
    padding: 12px 17px;
    margin-left: 19px;
  }

  &::placeholder {
    font-size: 16px;
    line-height: 18px;
    color: $color-main;
    text-transform: uppercase;

    @media (min-width: $tablet-width) {
      font-size: 20px;
      line-height: 30px;
    }
  }
}

.input-text__input:hover {
  border-color: $gray--darken;
}

.input-text__input:focus {
  border-color: $color-main;
}

.input-text__input:focus:invalid {
  border-color: $error-color;
}
